@charset "UTF-8";
/* Base */
/* Breakpoints */
/* Fonts */
/* Colors */
/* Адаптив */
/* Ховер на десктопах */
@font-face {
  font-family: "Rosatom";
  src: url("../fonts/Rosatom-Regular.woff2") format("woff2"), url("../fonts/Rosatom-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Rosatom";
  src: url("../fonts/Rosatom-Bold.woff2") format("woff2"), url("../fonts/Rosatom-Bold.ttf") format("truetype");
  font-weight: 700;
  font-display: swap;
}
@keyframes gradient-button {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 500;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Rosatom", sans-serif;
  background-color: #ffffff;
  color: #333333;
  font-size: 20px;
  line-height: 1.3;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #333333;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Blocks */
.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 218px;
  width: 100%;
  padding: 12px;
  border-radius: 22px;
  background-color: #015fa1;
  color: #ffffff;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(51, 51, 51, 0.7);
}

.title {
  font-weight: 400;
  font-size: 46px;
  line-height: 1;
}
@media only screen and (max-width: 1279px) {
  .title {
    font-size: calc(30px + (46 - 30) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title {
    font-size: calc(25px + (30 - 25) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title {
    font-size: calc(20px + (30 - 20) * (100vw - 0px) / (768 - 0));
  }
}
.title--big {
  font-size: 50px;
}
@media only screen and (max-width: 1279px) {
  .title--big {
    font-size: calc(30px + (50 - 30) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title--big {
    font-size: calc(25px + (30 - 25) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title--big {
    font-size: calc(20px + (30 - 20) * (100vw - 0px) / (768 - 0));
  }
}

.title-gradient {
  position: relative;
  border-radius: 10px;
  padding: 16px;
  font-weight: 700;
  color: #ffffff;
  font-size: 40px;
  text-align: center;
  background: linear-gradient(45deg, #14256d 0%, #4494cf 55%, #4494cf 100%);
  background-size: 300%;
  animation: gradient-button 10s linear infinite;
}

.counter {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 46px 40px;
  max-width: 400px;
}
.counter__item:nth-child(3n+1) {
  grid-column: 1/span 2;
}
.counter__item p:first-child {
  font-size: 25px;
  line-height: 1.25;
}
.counter__item p:last-child {
  font-size: 18px;
  border-top: 1px solid #333333;
  padding-top: 8px;
}
.counter__item p span {
  font-size: 65px;
}

.circle {
  position: relative;
  height: 0;
  padding-top: 100%;
}
.circle::before {
  position: absolute;
  top: 14%;
  right: 13%;
  z-index: 1;
  content: "";
  min-width: 15px;
  max-width: 15px;
  height: 15px;
  background-color: #015fa1;
  border-radius: 50%;
}
.circle__item {
  position: absolute;
  background-color: #ffffff;
  border-radius: 50%;
  border: 1px solid #6dace4;
}
.circle__item--colored {
  background-color: rgba(109, 172, 228, 0.2);
}
.circle__item--1 {
  top: 11%;
  right: 11%;
  width: 24%;
  height: 24%;
}
.circle__item--1 span {
  top: 39%;
  left: -4%;
}
.circle__item--2 {
  top: 8%;
  right: 8%;
  width: 45%;
  height: 45%;
}
.circle__item--2 span {
  bottom: 6%;
  left: 11%;
}
.circle__item--3 {
  top: 5%;
  right: 5%;
  width: 67%;
  height: 67%;
}
.circle__item--3 span {
  top: 45%;
  left: -1%;
}
.circle__item--4 {
  top: 2%;
  right: 2.5%;
  width: 84%;
  height: 84%;
}
.circle__item--4 span {
  bottom: 2%;
  left: 23%;
}
.circle__item--5 {
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.circle__item--5 span {
  bottom: 27%;
  left: 3%;
}
.circle__item span {
  position: absolute;
  display: flex;
  gap: 10px;
  max-width: 160px;
  font-size: 16px;
  line-height: 1.125;
}
.circle__item span::before {
  content: "";
  min-width: 10px;
  max-width: 10px;
  height: 10px;
  background-color: #015fa1;
  border-radius: 50%;
  filter: blur(2px);
  margin-top: 4px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 58px 0;
  overflow: hidden;
  transition: 0.5s ease;
}
@media only screen and (max-width: 767px) {
  .header {
    padding: 30px 0;
  }
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.footer {
  position: relative;
  z-index: 10;
  padding: 68px 0;
  border-radius: 40px 40px 0 0;
  background-color: #333333;
  margin-top: 80px;
}
@media only screen and (max-width: 767px) {
  .footer {
    margin-top: 60px;
  }
}

.hero {
  padding: 214px 0 58px;
  background-color: #e8f3ff;
  border-radius: 0 0 40px 40px;
}
.hero__title {
  font-weight: 400;
  font-size: 80px;
  line-height: 1.125;
  text-align: center;
}
.hero__text {
  max-width: 650px;
  margin: 31px auto 0;
  font-size: 24px;
  line-height: 1.29;
  text-align: center;
}
.hero__bg {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  width: 1438px;
  height: 282px;
  margin-top: 77px;
}

.statistics {
  padding: 62px 0 20px;
}
.statistics__quote {
  display: flex;
  gap: 35px;
  margin-top: 55px;
}
.statistics__quote-image {
  min-width: 140px;
  max-width: 140px;
  height: 140px;
  border-radius: 20px;
  overflow: hidden;
}
.statistics__quote-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.statistics__quote-text {
  max-width: 800px;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.3;
}
.statistics__wrapper {
  display: flex;
  margin-top: 30px;
}
.statistics__col:first-child {
  width: 53%;
}
.statistics__col:last-child {
  width: 47%;
}
.statistics__text {
  line-height: 1.3;
}
.statistics__text p + p {
  margin-top: 7px;
}
.statistics__button {
  margin-top: 20px;
}
.statistics__counter {
  margin-top: 40px;
}